// Search box Lit component
import { LitElement, html, css } from 'lit';
import { customElement, state, query } from 'lit/decorators.js';

interface SearchResult {
  lat: number;
  lon: number;
  name: string;
  probability: number;
}

@customElement('search-box')
export class SearchBox extends LitElement {
  static styles = css`
    :host {
      display: block;
      position: relative;
    }

    .search-container {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      padding: 0.75rem;
      border: 1px solid #ccc;
      border-radius: 8px;
      background: #fff;
    }

    input {
      flex: 1;
      border: none;
      outline: none;
      font-size: 1rem;
      padding: 0;
    }

    .suggestions {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      background: #fff;
      border: 1px solid #ccc;
      border-top: none;
      border-radius: 0 0 8px 8px;
      max-height: 300px;
      overflow-y: auto;
      z-index: 1000;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    }

    .suggestions ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    .suggestions li {
      padding: 0.75rem;
      cursor: pointer;
      border-bottom: 1px solid #f0f0f0;
    }

    .suggestions li:hover,
    .suggestions li.selected {
      background: #f5f5f5;
    }

    .suggestions li:last-child {
      border-bottom: none;
    }
  `;

  @state()
  private results: SearchResult[] = [];

  @state()
  private showSuggestions = false;

  @state()
  private selectedIndex = -1;

  @state()
  private value = '';

  @query('input')
  private input!: HTMLInputElement;

  private debounceTimer?: number;

  private handleInput(e: Event) {
    this.value = (e.target as HTMLInputElement).value;
    clearTimeout(this.debounceTimer);

    if (!this.value) {
      this.showSuggestions = false;
      return;
    }

    // Debounce search
    this.debounceTimer = window.setTimeout(() => {
      this.dispatchEvent(new CustomEvent('search', {
        detail: { query: this.value }
      }));
    }, 300);
  }

  private handleKeyDown(e: KeyboardEvent) {
    switch (e.key) {
      case 'ArrowUp':
        e.preventDefault();
        this.selectPrevious();
        break;
      case 'ArrowDown':
        e.preventDefault();
        this.selectNext();
        break;
      case 'Enter':
        e.preventDefault();
        this.selectCurrent();
        break;
      case 'Escape':
        this.showSuggestions = false;
        break;
    }
  }

  private selectNext() {
    if (this.results.length === 0) return;
    this.selectedIndex = (this.selectedIndex + 1) % this.results.length;
  }

  private selectPrevious() {
    if (this.results.length === 0) return;
    this.selectedIndex = this.selectedIndex <= 0
      ? this.results.length - 1
      : this.selectedIndex - 1;
  }

  private selectCurrent() {
    if (this.selectedIndex >= 0 && this.selectedIndex < this.results.length) {
      this.handleResultClick(this.results[this.selectedIndex]);
    }
  }

  private handleResultClick(result: SearchResult) {
    this.value = result.name;
    this.showSuggestions = false;

    this.dispatchEvent(new CustomEvent('location-select', {
      detail: result
    }));
  }

  private handleFocus() {
    if (this.results.length > 0) {
      this.showSuggestions = true;
    }
  }

  private handleBlur() {
    // Delay to allow click events
    setTimeout(() => {
      this.showSuggestions = false;
    }, 200);
  }

  // Public method to update results from parent
  setResults(results: SearchResult[]) {
    this.results = results.sort((a, b) => b.probability - a.probability);
    this.showSuggestions = results.length > 0;
    this.selectedIndex = -1;
  }

  render() {
    return html`
      <div class="search-container">
        <i class="search"></i>
        <input
          type="text"
          placeholder="Search or enter an address"
          .value=${this.value}
          @input=${this.handleInput}
          @keydown=${this.handleKeyDown}
          @focus=${this.handleFocus}
          @blur=${this.handleBlur}
          role="combobox"
          aria-expanded=${this.showSuggestions}
          aria-autocomplete="list"
          aria-label="Search for a location"
        />
      </div>

      ${this.showSuggestions ? html`
        <div class="suggestions">
          <ul role="listbox">
            ${this.results.map((result, index) => html`
              <li
                role="option"
                class=${index === this.selectedIndex ? 'selected' : ''}
                aria-selected=${index === this.selectedIndex}
                @click=${() => this.handleResultClick(result)}
              >
                ${result.name}
              </li>
            `)}
          </ul>
        </div>
      ` : ''}
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'search-box': SearchBox;
  }
}
