// Main Lit web component
import { LitElement, html, css } from 'lit';
import { customElement, property, state } from 'lit/decorators.js';
import { TransitAPI } from './api/transit';
import { parseHashParams, updateHash } from './utils/url';
import './components/search-box';
import './components/route-list';
import './components/location-button';

interface Route {
  route_color: string;
  route_text_color: string;
  route_short_name: string;
  route_long_name: string;
  itineraries: Itinerary[];
}

interface Itinerary {
  headsign: string;
  schedule_items: ScheduleItem[];
}

interface ScheduleItem {
  departure_time: number;
  is_real_time: boolean;
}

@customElement('transit-widget')
export class TransitWidget extends LitElement {
  static styles = css`
    :host {
      display: block;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
      max-width: 100%;
      height: 100vh;
      overflow: hidden;
    }

    .widget-container {
      display: flex;
      flex-direction: column;
      height: 100%;
    }

    .header {
      padding: 1rem;
      background: #fff;
      border-bottom: 1px solid #e0e0e0;
      flex-shrink: 0;
    }

    .content {
      flex: 1;
      overflow-y: auto;
      padding: 1rem;
    }

    .footer {
      padding: 1rem;
      text-align: center;
      font-size: 0.875rem;
      color: #666;
      border-top: 1px solid #e0e0e0;
      flex-shrink: 0;
    }

    .footer a {
      color: #0066cc;
      text-decoration: none;
    }

    .loading {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 2rem;
      gap: 1rem;
    }

    .error {
      background: #fee;
      color: #c00;
      padding: 1rem;
      border-radius: 4px;
      margin: 1rem 0;
    }
  `;

  @property({ type: String })
  apiKey = '';

  @property({ type: Number })
  latitude = 45.51485;

  @property({ type: Number })
  longitude = -73.55965;

  @property({ type: Number })
  distance = 500;

  @property({ type: Boolean })
  sortByTime = false;

  @state()
  private routes: Route[] = [];

  @state()
  private loading = false;

  @state()
  private error = '';

  private api!: TransitAPI;
  private refreshTimer?: number;

  connectedCallback() {
    super.connectedCallback();

    // Initialize API (no key needed - using Express proxy)
    this.api = new TransitAPI();

    // Parse hash params for embedding
    const params = parseHashParams();
    if (params.lat) this.latitude = params.lat;
    if (params.lng) this.longitude = params.lng;
    if (params.distance) this.distance = params.distance;
    if (params.sortByTime) this.sortByTime = params.sortByTime;

    // Listen for hash changes
    window.addEventListener('hashchange', this.handleHashChange);

    // Load initial data
    this.loadNearbyRoutes();
  }

  disconnectedCallback() {
    super.disconnectedCallback();
    window.removeEventListener('hashchange', this.handleHashChange);
    if (this.refreshTimer) clearTimeout(this.refreshTimer);
  }

  private handleHashChange = () => {
    const params = parseHashParams();
    if (params.lat && params.lng) {
      this.latitude = params.lat;
      this.longitude = params.lng;
      this.loadNearbyRoutes();
    }
  };

  private async loadNearbyRoutes() {
    this.loading = true;
    this.error = '';

    try {
      const response = await this.api.getNearbyRoutes({
        lat: this.latitude,
        lon: this.longitude,
        max_distance: this.distance
      });

      this.routes = response.routes;

      // Schedule refresh
      if (this.refreshTimer) clearTimeout(this.refreshTimer);
      this.refreshTimer = window.setTimeout(() => this.loadNearbyRoutes(), 30000);

    } catch (err) {
      this.error = 'Failed to load routes. Please try again.';
      console.error('Error loading routes:', err);
    } finally {
      this.loading = false;
    }
  }

  private handleSearch = async (e: CustomEvent<{ query: string }>) => {
    const { query } = e.detail;

    try {
      const results = await this.api.searchStops({
        lat: this.latitude,
        lon: this.longitude,
        query
      });

      // Dispatch results back to search-box component
      this.dispatchEvent(new CustomEvent('search-results', {
        detail: { results: results.results }
      }));

    } catch (err) {
      console.error('Search error:', err);
    }
  };

  private handleLocationSelect = (e: CustomEvent<{ lat: number; lon: number; name: string }>) => {
    const { lat, lon, name } = e.detail;
    this.latitude = lat;
    this.longitude = lon;
    updateHash(lat, lon, name);
    this.loadNearbyRoutes();
  };

  private handleLocationFound = (e: CustomEvent<GeolocationCoordinates>) => {
    const coords = e.detail;
    this.latitude = coords.latitude;
    this.longitude = coords.longitude;
    updateHash(coords.latitude, coords.longitude, 'Current location');
    this.loadNearbyRoutes();
  };

  render() {
    return html`
      <div class="widget-container">
        <div class="header">
          <search-box
            @search=${this.handleSearch}
            @location-select=${this.handleLocationSelect}
          ></search-box>

          <location-button
            @location-found=${this.handleLocationFound}
          ></location-button>
        </div>

        <div class="content">
          ${this.error ? html`
            <div class="error" role="alert">${this.error}</div>
          ` : ''}

          ${this.loading ? html`
            <div class="loading" role="status" aria-live="polite">
              <svg width="40" height="40" viewBox="0 0 50 50">
                <path fill="#000" d="M25.251,6.461c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615V6.461z">
                  <animateTransform attributeType="xml" attributeName="transform" type="rotate"
                                  from="0 25 25" to="360 25 25" dur="0.6s" repeatCount="indefinite"/>
                </path>
              </svg>
              <p>Loading routes...</p>
            </div>
          ` : html`
            <route-list
              .routes=${this.routes}
              .sortByTime=${this.sortByTime}
            ></route-list>
          `}
        </div>

        <div class="footer" role="contentinfo">
          Powered by <a href="https://transitapp.com" target="_blank">Transit</a>
          <br>
          Free on <a href="https://transitapp.com/download/ios.html" target="_blank">iOS</a>
          and <a href="https://transitapp.com/download/android.html" target="_blank">Android</a>
        </div>
      </div>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'transit-widget': TransitWidget;
  }
}
